.venue-section {
	position: relative;
	padding: 5rem 0;

	@include max-md {
		padding: 3rem 0;
	}

	.venue-wrapper {
		position: relative;
		z-index: 3;

		.venue-description {
			position: relative;
			margin-bottom: 3rem;
			text-align: left;

			@include max-sm {
				margin-bottom: 1rem;
			}

			.title-section {
				text-align: left;
			}
		}

		.venue-content {
			position: relative;

			.ribbon-venue {
				position: relative;
				margin-bottom: 1rem;

				svg {
					width: 40px;
					height: auto;

					path {
						stroke: var(--color-dark);
					}
				}

				.st0 {
					fill: var(--color-secondary);
				}

				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}

			.card {
				background-color: transparent;
				color: var(--color-dark);
				text-align: left;
				border: none;
				overflow: hidden;

				&::before {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					height: 90%;
					background-color: var(--color-primary);
					border-radius: 4px;
					content: "";
				}

				.card-body {
					position: relative;
					padding: 2rem 1.8rem;

					h4 {
						padding: 0 0 1rem;
					}

					.event-description {
						position: relative;
						margin-bottom: 1rem;

						p {
							margin-bottom: 0;
						}
					}

					.event-name {
						position: relative;
						width: 100%;
						margin-bottom: 1.5rem;

						.date {
							position: relative;
							width: 100%;

							h5 {
								font-family: $font-secondary;
								font-weight: 400;
								margin-bottom: 0;
								font-size: 32px;
								padding: .5rem 0;
							}
						}

						h4 {
							font-family: $font-secondary;
							font-size: 36px;
							margin-bottom: 3rem;
						}

						p,
						span {
							font-size: 18px;
							margin-bottom: 0;
							font-weight: 600;

							@include max-sm {
								font-size: 16px;
							}
						}
					}

					.event-place {
						position: relative;

						&-body {
							position: relative;

							.mountain {
								position: relative;
								width: calc(100% + 4rem);
								margin: 0 -1.8rem .5rem;
							}
						}

						.widget-elements {
							position: relative;
							margin-top: 1rem;
						}
					}
				}
			}
		}

		.land {
			position: relative;
			width: 140%;
			margin-left: -25%;
			margin-bottom: -10rem;
			transform: scaleX(-1);

			.overlay-elements-gradient {
				height: 100%;

				&.overlay-bottom {
					background: linear-gradient(to top, var(--color-primary) 10%, transparent 80%);
				}
			}
		}

		.orn-1 {
			position: absolute;
			top: 0;
			right: 0;
			width: 10%;
			transform: translate(45%, 0) rotate(-25deg);

			@include max-lg {
				width: 20%;
			}

			@include max-sm {
				width: 30%;
			}
		}
	}

	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;

		.title-section {
			&::before {
				all: unset;
			}
		}

		@include max-md {
			padding: 2rem 0;
		}

		* {
			position: relative;
		}

		.prokes-wrap {
			display: flex;
			gap: 0.75rem;
			justify-content: center;
			align-items: flex-start;
			flex-wrap: wrap;
			text-align: center;

			.prokes {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: flex-start;
				width: 132px;

				@include max-md {
					width: 120px;
				}

				@include max-xs {
					width: 45%;
				}
			}
		}
	}

	.row-venue {
		row-gap: 3rem;
	}

	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}

		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}

		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}

		40% {
			opacity: 1;
		}

		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}

		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}

		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}

		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}

		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}