.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;

	&.cover-opened {
		transform: translateY(-100%);
	}

	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;

		@include max-lg {
			padding: 4rem 0 10rem;
			align-items: center;
		}

		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;

			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 35%;
				height: 100%;
				color: var(--color-dark);
				border-radius: 300px;

				@include max-xl {
					width: 40%;
				}

				@include max-lg {
					width: 60%;
				}

				@include max-sm {
					width: 100%;
				}

				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-color: var(--color-primary);
					border-radius: 300px;
					opacity: .4;
					content: "";
				}

				&::after {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					border-radius: 300px;
					box-shadow: 0 0 2rem var(--color-primary);
					opacity: .8;
					content: "";
					pointer-events: none;
				}

				.cover-description {
					position: relative;
					padding: 1rem;
					text-align: center;

					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}

					.greeting-wrapper {
						position: relative;
						margin-bottom: 1.5rem;
					}

					.title {
						padding: 1rem 0;
						margin-bottom: 0;
					}

					p {
						margin-bottom: 0;
					}

					h6 {
						margin-bottom: 0;
						font-weight: 600;
					}
				}
			}

			.elements-widget {
				position: relative;
				width: 100%;
				padding-top: 1rem;
			}
		}
	}

	.bg {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 150%;
		transform: translate(-53%, 35%) scaleX(-1);
		opacity: .2;

		@include max-lg {
			width: 180%;
			transform: translate(-53%, 15%) scaleX(-1);
		}

		@include max-sm {
			width: 200%;
		}
	}

	.mountain {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 120%;
		transform: translate(-50%, 40%);

		@include max-lg {
			width: 140%;
			transform: translate(-50%, 30%);
		}

		@include max-sm {
			width: 160%;
		}
	}

	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 25%;
		transform: translate(-35%, -20%);

		@include max-lg {
			width: 45%;
		}

		@include max-sm {
			width: 55%;
		}
	}

	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 25%;
		transform: translate(42%, -20%) scaleX(-1);

		@include max-lg {
			width: 45%;
		}

		@include max-sm {
			width: 55%;
		}
	}
}